@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	background-color: #1e88e5;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.app-header{background-color:#1e88e5;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;position:fixed;height:90px;width:100%}.app-header .container{margin:26px;display:flex;justify-content:space-between}.app-header .container .title-header{font-size:2em}

.app-footer{background-color:#005cb2;width:100%;position:fixed;bottom:0;border-top-left-radius:20px;border-top-right-radius:20px}.app-footer .container{margin:26px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.app-footer .container .hour-container{display:flex;flex-direction:row;align-items:center}.app-footer .container .hour-container .hour{color:#fff;margin-left:15px;font-size:2em}.app-footer .container .logo{font-size:2em;color:#fff}

.item-container{background-color:#6ab7ff;border-radius:35px;margin-bottom:60px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;cursor:pointer;padding:30px}.item-container.large{padding:55px}.item-container .title{font-size:1.7em}.item-container:hover{border-style:solid;border-width:5px;border-color:#ff6f00}

.container-button-cancel{background-color:#ff6f00;position:fixed;right:0;bottom:20%;margin-right:25px;padding:15px;border-radius:10px;color:#fff;cursor:pointer;font-weight:700}

.container-ticket{border:0;box-sizing:border-box;font-family:arial;display:flex;justify-content:center;flex-direction:column}.container-ticket .time-back{margin-top:25px}.container-ticket .fl-left{float:left}.container-ticket .fl-right{float:right}.container-ticket .row{overflow:hidden;display:flex;justify-content:center}.container-ticket .card{display:flex;background-color:#fff;color:#989898;margin-bottom:10px;font-family:"Oswald", sans-serif;text-transform:uppercase;border-radius:4px;position:relative}.container-ticket .card+.card{margin-left:2%}.container-ticket .date{display:table-cell;width:25%;position:relative;text-align:center;border-right:2px dashed #dadde6}.container-ticket .date:before,.container-ticket .date:after{content:"";display:block;width:30px;height:30px;background-color:#1e88e5;position:absolute;top:-15px;right:-15px;border-radius:50%}.container-ticket .date:after{top:auto;bottom:-15px}.container-ticket .date time{display:block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.container-ticket .date time span{display:block}.container-ticket .date time span:first-child{color:#2b2b2b;font-weight:600;font-size:250%}.container-ticket .date time span:last-child{text-transform:uppercase;font-weight:600;margin-top:-10px}.container-ticket .card-cont{display:table-cell;width:75%;font-size:85%;padding:10px 10px 30px 50px}.container-ticket .card-cont h3{color:#3c3c3c;font-size:130%}.container-ticket .card-cont>div{display:table-row}.container-ticket .card-cont .even-date i,.container-ticket .card-cont .even-info i,.container-ticket .card-cont .even-date time,.container-ticket .card-cont .even-info p{display:table-cell}.container-ticket .card-cont .even-date i,.container-ticket .card-cont .even-info i{padding:5% 5% 0 0}.container-ticket .card-cont .even-info p{padding:30px 50px 0 0}.container-ticket .card-cont .even-date time span{display:block}.container-ticket .card-cont a{display:block;text-decoration:none;width:80px;height:30px;background-color:#d8dde0;color:#4b2121;text-align:center;line-height:30px;border-radius:2px;position:absolute;right:10px;bottom:10px}@media screen and (max-width: 860px){.container-ticket .card{display:block;float:none;width:100%;margin-bottom:10px}.container-ticket .card+.card{margin-left:0}.container-ticket .card-cont .even-date,.container-ticket .card-cont .even-info{font-size:75%}}

.main-container{width:80%;margin:26px;margin-top:150px}

