@import url("https://fonts.googleapis.com/css?family=Oswald");
.container-ticket {
	border: 0;
	box-sizing: border-box;
	font-family: arial;
	display: flex;
	justify-content: center;
	flex-direction: column;

	.time-back {
		margin-top: 25px;
	}

	.fl-left {
		float: left;
	}

	.fl-right {
		float: right;
	}

	.row {
		overflow: hidden;
		display: flex;
		justify-content: center;
	}

	.card {
		display: flex;
		//width: 49%;
		background-color: #fff;
		color: #989898;
		margin-bottom: 10px;
		font-family: "Oswald", sans-serif;
		text-transform: uppercase;
		border-radius: 4px;
		position: relative;
	}

	.card + .card {
		margin-left: 2%;
	}

	.date {
		display: table-cell;
		width: 25%;
		position: relative;
		text-align: center;
		border-right: 2px dashed #dadde6;
	}

	.date:before,
	.date:after {
		content: "";
		display: block;
		width: 30px;
		height: 30px;
		background-color: #1e88e5;
		position: absolute;
		top: -15px;
		right: -15px;

		border-radius: 50%;
	}

	.date:after {
		top: auto;
		bottom: -15px;
	}

	.date time {
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	}

	.date time span {
		display: block;
	}

	.date time span:first-child {
		color: #2b2b2b;
		font-weight: 600;
		font-size: 250%;
	}

	.date time span:last-child {
		text-transform: uppercase;
		font-weight: 600;
		margin-top: -10px;
	}

	.card-cont {
		display: table-cell;
		width: 75%;
		font-size: 85%;
		padding: 10px 10px 30px 50px;
	}

	.card-cont h3 {
		color: #3c3c3c;
		font-size: 130%;
	}

	.card-cont > div {
		display: table-row;
	}

	.card-cont .even-date i,
	.card-cont .even-info i,
	.card-cont .even-date time,
	.card-cont .even-info p {
		display: table-cell;
	}

	.card-cont .even-date i,
	.card-cont .even-info i {
		padding: 5% 5% 0 0;
	}

	.card-cont .even-info p {
		padding: 30px 50px 0 0;
	}

	.card-cont .even-date time span {
		display: block;
	}

	.card-cont a {
		display: block;
		text-decoration: none;
		width: 80px;
		height: 30px;
		background-color: #d8dde0;
		color: rgb(75, 33, 33);
		text-align: center;
		line-height: 30px;
		border-radius: 2px;
		position: absolute;
		right: 10px;
		bottom: 10px;
	}

	@media screen and (max-width: 860px) {
		.card {
			display: block;
			float: none;
			width: 100%;
			margin-bottom: 10px;
		}

		.card + .card {
			margin-left: 0;
		}

		.card-cont .even-date,
		.card-cont .even-info {
			font-size: 75%;
		}
	}
}
